import { Injectable } from '@angular/core';
import { Headers, Http, Response, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/toPromise';

import { WebService } from '../webapi';
import { Context } from '../context';

import { CatalogInfo, CatalogService } from '../entities';

//////////////////////////// MODEL /////////////////////////////////////////////////////
export class ProcesoInfo extends CatalogInfo {
    nombre: string;
}


//////////////////////////// SERVICE /////////////////////////////////////////////////////
@Injectable()
export class ProcesoService extends CatalogService<ProcesoInfo> {
    constructor(http: Http, context: Context) {
        super(http, context, "api/kernel/security/procesos");
    }

    create(): Promise<ProcesoInfo> {
        return Promise.resolve(new ProcesoInfo());
    }
}
